import React from 'react';
import { Container, Button } from 'react-bootstrap';
import Breadcrumbs from '../Breadcrumbs';
import fileUpload from '../../apis/fileUpload';

class CommonTemplate extends React.Component {
  state = { task: '', selectedFile: null, resultText: '' };

  componentDidMount() {
    this.setState({ task: this.props.task });
    console.log(this.state.task);
  }

  renderExplanation = (task) => {
    const style = { backgroundColor: 'yellow' };
    if (task === 'chemical-disease') {
      return (
        <h6>
          EX) In defects of <span style={style}>BC6ENTG</span> ,{' '}
          <span style={style}>BC6OTHER</span> is transformed to{' '}
          <span style={style}>BC6ENTC</span> , resulting in high levels of{' '}
          <span style={style}>BC6OTHER</span> in the body.
        </h6>
      );
    } else if (task === 'drug-drug') {
      return (
        <h6>
          EX) Concomitant <span style={style}> @DRUG$ </span> administration
          decreased the mean AUC of total <span style={style}> @DRUG$ </span>
          approximately 55%.
        </h6>
      );
    } else if (task === 'gene-disease') {
      return (
        <h6>
          EX) MPO genotype GG is associated with{' '}
          <span style={style}>@DISEASE$</span> in patients with hereditary{' '}
          <span style={style}>@GENE$.</span>
        </h6>
      );
    } else {
      return null;
    }
  };

  setFile = (event) => {
    console.log(event.target.files);
    this.setState({ selectedFile: event.target.files[0] });
  };

  onSubmit = async () => {
    const { selectedFile } = this.state;
    const formData = new FormData();
    formData.append('file', selectedFile);
    console.log(selectedFile);

    if (!selectedFile) {
      console.log('Please upload file!');
      return;
    }

    const response = await fileUpload.post(
      `http://localhost:4000/Gene-Disease`
    );

    this.setState({ resultText: response.data.results });
  };

  renderFileUpload() {
    return (
      <form>
        Select a file to upload:
        <input type="file" name="fileToUpload" onChange={this.setFile} />
        <Button variant="danger" onClick={this.onSubmit}>
          Upload a file
        </Button>
      </form>
    );
  }

  renderResult() {
    return (
      <div
        id="log"
        className="result-box"
        style={{
          overflowX: 'hidden',
          height: '200px',
          width: '100%',
          border: '1px solid #aaaaaa',
        }}
      >
        <p></p>
      </div>
    );
  }

  render() {
    return (
      <div>
        <Breadcrumbs task={this.props.task} />
        <section id="fileupload">
          <Container>
            <h5>Please upload the .txt file with the following format.</h5>
            {this.renderExplanation(this.props.task)}
            {this.renderFileUpload()}
            {this.renderResult()}
          </Container>
        </section>
      </div>
    );
  }
}

export default CommonTemplate;
